<template>
  <v-layout>
    <v-container>
      <add-area v-on:getVal="getVal" />
      <center>
        <v-img
          v-if="!isGetData && !listOfAreas.length"
          class="image-not-fond"
          contain
          :src="'/nodata.png'"
        >
        </v-img>
        <v-progress-circular
          id="progress"
          v-if="isGetData"
          :size="70"
          width="7"
          color="primary"
          indeterminate
        ></v-progress-circular>
      </center>
      <v-row>
        <card-component
          v-on:getVal="getVal"
          id="card-areas"
          v-for="(dataAreas, index) in listOfAreas"
          :key="index"
          :dataAreas="dataAreas"
        />
      </v-row>
    </v-container>
  </v-layout>
</template>

<script>
import { areaController } from "../../components/areas/area";

export default {
  mounted() {
    this.getAreas();
  },

  methods: {
    getVal() {
      this.getAreas();
    },
    getAreas: async function () {
      this.listOfAreas = [];
      this.isGetData = true;
      var data = await areaController.getAreas();
      this.isGetData = false;

      this.listOfAreas = data;
    },
  },
  components: {
    "card-component": () => import("../../components/areas/card.vue"),
    "add-area": () => import("../../components/areas/add.vue"),
  },
  data() {
    return {
      isGetData: false,
      listOfAreas: [],
    };
  },
  computed: {},
};
</script>

<style>
#progress {
  margin-top: 20%;
}
.image-not-fond {
  margin-top: 5%;
  height: 500px;
  width: 500px;
}
</style>