import axios from "axios";
import store from '../../store/index'
export const areaController = {
    addArea: async function (area) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("name[ar]", area.name.ar);
        formData.append("name[en]", area.name.en);
        formData.append("name[tr]", area.name.tr);
        await axios
            .request({
                method: "POST",
                url: mainUrl + "/areas",
                data: formData,
                headers: this.configAxios(),
            }).then(res => {
                if (res.status == 200) {
                    if (res.data.state) {
                        state = true;
                        this.ShowAlert('secondary', 'success add section ')
                    } else {
                        state = false;
                        this.ShowAlert('red', res.data.errors)
                    }
                }
            }).catch(error => {
                state = false;
                store.dispatch('showAlert', {
                    color: 'red', message: 'An Error ðŸ˜¥ please contact Us '
                        + error
                })
            })

        return state;

    },


    getAreas: async function () {
        var mainUrl = store.state.mainUrl;
        var areas = [];
        await axios.get(mainUrl + "/areas").then((data) => {
            if (data.status == 200) {
                areas = data.data.data;
                store.commit("SET_AREAS", areas);

            } else {
                this.ShowAlert('red', 'An ERROR ' + data.status)
            }
        }).catch(error => {
            this.ShowAlert('red', 'An ERROR ' + error)
        });
        // all time return below function for pass data between front and back end 
        return areas;
    },
    deleteArea: async function (id) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        await axios
            .request({
                url: mainUrl + "/areas/" + id,
                method: "DELETE",
                headers: this.configAxios(),
            }).then(res => {
                if (res.status == 200) {
                    state = true;
                    this.ShowAlert('secondary', 'done delete success');
                }
            }).catch(error => {
                state = false
                this.ShowAlert('red', 'An Error ERROR CODE' + error);
            }
            );
        return state;
    },
    updateArea: async function (area) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("name[ar]", area.name.ar);
        formData.append("name[en]", area.name.en);
        formData.append("name[tr]", area.name.tr); await axios
            .request({
                method: "POST",
                url: mainUrl + "/areas/" + area.id,
                data: formData,
                headers: this.configAxios(),
            }).then(res => {
                this.ShowAlert('secondary', 'done update success  ');
                if (res.status == 200) {

                    if (res.data.state) {
                        this.ShowAlert('secondary', 'done update success  ');
                    } else {
                        this.ShowAlert('red', res.data.errors);
                    }

                }
                state = true;
            }).catch(error => {
                state = false;
                this.ShowAlert('red', 'An Error ðŸ˜¥ please contact Us '
                    + error);
            })

        return state;

    },
    ShowAlert: function (color, message) {
        store.dispatch('showAlert', {
            color: color, message: message
        })
    },
    configAxios() {
        var token = store.state.token;
        return {
            "Content-type": "multipart/form-data",
            "Content-Disposition": "form-data",
            Authorization: "Bearer " + token,
        }
    }
}